//ÂÆö‰πâ‰∏Ä‰∏™ÁÆÄÂåñurlÁöÑÂáΩÊï∞
function simpUrl(url) {
  return url
    .replace("http://", "")
    .replace("https://", "")
    .replace("www.", "")
    .replace(/\/.*/, "");
}

